import "../../document.css";
import React from "react";
export default () => (
  <div style={{ padding: "1rem" }}>
    <p>
      Šis lietotāja līgums būs spēkā visiem lietotājiem no 2020. gada 30. maija.
    </p>

    <h3>Laipni lūdzam Local-Currency.com!</h3>

    <p>
      Šie ir Tava un Local-Currency.com (“LC”) līguma noteikumi, kas regulē Tava
      LC konta un LC pakalpojumu izmantošanu, ko mēs saucam par mūsu lietotāja
      līgumu. Tas attiecas uz LC kontiem visā pasaulē.
    </p>
    <p>
      Lai izmantotu LC pakalpojumus, Tev ir jāatver LC konts. Atverot un
      izmantojot LC kontu, Tu piekrīti ievērot visus šī lietotāja līguma
      noteikumus un nosacījumus.
    </p>

    <p>
      Lūdzu, uzmanīgi izlasi visus šī lietotāja līguma noteikumus un
      nosacījumus, kā arī visus pārējos dokumentus, kas uz Tevi attiecas.
    </p>
    <p>
      Mēs laiku pa laikam varam pārskatīt šo lietotāja līgumu un jebkuru no
      iepriekš uzskaitītajiem dokumentiem. Pārskatītā versija būs spēkā brīdī,
      kad to publicēsim, ja vien nebūs norādīts citādi. Ja mūsu veiktās izmaiņas
      samazina Tavas tiesības vai palielina Tavu atbildību, mēs publicēsim
      paziņojumu mūsu vietnes politikas atjauninājumu lapā un sniegsim Tev
      paziņojumu vismaz 60 dienas iepriekš. Turpinot izmantot mūsu pakalpojumus
      pēc tam, kad stājušās spēkā izmaiņas šajā lietotāja līgumā, Tu piekrīti,
      ka šīs izmaiņas Tev ir saistošas. Ja nepiekrīti kādai no izmaiņām, pirms
      izmaiņu stāšanās spēkā vari slēgt savu kontu.
    </p>

    <h4>Informācija par mums un mūsu pakalpojumu</h4>
    <p>
      LC pamatdarbība ir vietējo valūtu radīšana un maksājumu pakalpojumu
      sniegšana, izmantojot šīs vietējās valūtas.
    </p>
    <p>
      Lai iegūtu vairāk informācijas par mums un mūsu pakalpojumiem, vari
      izlasīt mūsu galveno maksājumu un pakalpojumu informāciju.
    </p>
    <p>Šis lietotāja līgums Tev jebkurā laikā būs pieejams LC vietnē.</p>

    <h3>Konta atvēršana</h3>

    <p>
      Mēs atļaujam vienai fiziskajai personai atvērt ne vairāk kā vienu kontu.
    </p>

    <p>
      Mēs piedāvājam trīs kontu veidus: norēķinu kontus, kredītkontus un
      krājkontus.
    </p>

    <h5>Norēķinu konti</h5>

    <p>
      Ar norēķinu kontu Tu vari nosūtīt un saņemt vietējās valūtas no citiem
      ZĪME.LV lietotājiem un norēķināties tiešsaistē par pirkumiem, kas izteikti
      vietējās valūtās.
    </p>

    <h5>Kredīta konti</h5>

    <p>
      Izmantojot kredītkontu, Tu vari nosūtīt vietējās valūtas citiem vietējo
      valūtu tirgus dalībniekiem un norēķināties tiešsaistē par pirkumiem, kas
      izteikti vietējās valūtās.
    </p>

    <h5>Krājkonti</h5>

    <p>
      Izmantojot krājkontu, Tu vari nosūtīt vietējās valūtas citiem ZĪME.LV
      lietotājiem un norēķināties tiešsaistē par pirkumiem, kas izteikti
      vietējās valūtās.
    </p>

    <p>
      Dienas naudas maksājums Z 48 apmērā tiek pārskaitīts uz norēķinu kontu.
    </p>

    <h5>Droša sava ZĪME.LV konta lietošana</h5>

    <p>
      Tev jāveic visi nepieciešamie pasākumi, lai novērstu ZĪME.LV konta
      ļaunprātīgu izmantošanu. Tev ir jātur atbilstošā drošībā un kontrolē visu
      ierīču, priekšmetu, ID, paroļu un personīgo identifikācijas numuru / kodu,
      ko izmanto, lai piekļūtu savam ZĪME.LV kontam un ZĪME.LV pakalpojumiem.
    </p>

    <p>
      Tev ir jāievēro visi pamatotie norādījumi, ko mēs varam izdot par to, kā
      Tu vari uzturēt drošībā savu vietējo maksājumu instrumentu.
    </p>

    <p>
      Tev ir pienākums personīgi autentificēt visas ar Tavu kontu saistītās
      darbības. Visai informācijai, ko Tu sniedz attiecībā uz savu kontu
      jāatbilst patiesībai, ieskaitot Tavu e-pasta adresi, dzimšanas datumu un
      laika joslu dzimšanas laikā.
    </p>

    <p>
      ZĪME.LV konta profilā pasta adresei, e-pasta adresei un citai
      kontaktinformācijai jabūt aktuālai.
    </p>

    <p>
      Tu vari piešķirt, atsaukt un pārvaldīt tiesības trešajām personām veikt
      noteiktas darbības Tavā vārdā. Tu piekrīti, ka pēc tiesību piešķiršanas
      trešajai personai rīkoties Tavā vārdā, mēs varam atklāt šai trešajai
      personai noteiktu informāciju par Tavu kontu.
    </p>

    <p>
      Tu vari atļaut trešās personas pakalpojumu sniedzējiem, kuri rīkojas
      saskaņā ar piemērojamajiem tiesību aktiem:
    </p>

    <ul>
      <li>
        nodrošināt konta informācijas pakalpojumus, lai Tavā vārdā piekļūtu
        informācijai par Tavu kontu;
      </li>
      <li>
        apstiprināt, ka Tavā kontā ir pieejama summa, kas nepieciešama maksājuma
        darījuma izpildei; vai
      </li>
      <li>
        sniegt maksājuma uzsākšanas pakalpojumus, lai Tavā vārdā iniciētu
        maksājumus no Tava konta.
      </li>
    </ul>

    <p>
      Piešķirot atļauju jebkurai trešai personai piekļūt Tavam kontam, tas
      neatbrīvo Tevi no visiem Taviem pienākumiem saskaņā ar šo lietotāja
      līgumu. Tev ir jāatbild mums par Tavām darbībām, kuras Tu pilnvaro veikt
      trešajām personām. Tu atbrīvo mūs no saistībām, kas izriet no šādu trešo
      personu darbībām vai bezdarbības sakarā ar Tevis piešķirtajām atļaujām, un
      Tu apņemies atlīdzināt zaudējumus, ievērojot Tavas likumīgās tiesības.
    </p>

    <h3>ZĪME.LV konta slēgšana</h3>

    <p>
      Mēs varam slēgt Tavu kontu jebkurā laikā, par to paziņojot divus mēnešus
      iepriekš. Mēs varam slēgt Tavu kontu jebkurā laikā, ja:
    </p>

    <ol>
      <li>
        Tu pārkāp šo lietotāja līgumu un / vai mēs esam citādi tiesīgi slēgt
        Tavu kontu saskaņā ar šā lietotāja līguma nosacījumiem;
      </li>
      <li>Tu trīs gadus neesi izmantojis savu kontu; vai</li>
      <li>mums ir aizdomas, ka Tavam kontam ir piekļūts bez Tavas atļaujas.</li>
    </ol>

    <p>
      Ja mēs nolemsim slēgt Tavu kontu, mēs Tev sniegsim paziņojumu par konta
      slēgšanu un, ja tas praktiski iespējams, norādīsim Tava konta slēgšanas
      iemeslus, kā arī iespēju izņemt visus neapstrīdētos līdzekļus, kas ir mūsu
      rīcībā.
    </p>

    <p>Kad Tavs konts tiek slēgts:</p>
    <ul>
      <li>
        šis lietotāja līgums nekavējoties tiek izbeigts, izņemot to, ka šis
        lietotāja līgums tiek izbeigts tādā mērā un tik ilgi, kamēr mums ir
        nepieciešams rīkoties ar Tava konta slēgšanu un ievērot piemērojamos
        likumus un noteikumus;
      </li>
      <li>
        mēs varam atcelt visus neapstiprinātos darījumus, un Tu zaudēsi visus
        atlikumus, kas saistīti ar īpašiem finansēšanas noteikumiem;
      </li>
      <li>
        mēs varam apturēt, ierobežot vai pārtraukt Tavu piekļuvi mūsu
        pakalpojumiem, vietnēm, programmatūrai, sistēmām (ieskaitot visus tīklus
        un serverus, ko izmanto kādu no pakalpojumu sniegšanai) vai to
        izmantošanu, ko mēs izmantojam mūsu vārdā vai mūsu uzdevumā, vai arī
        dažus vai visus pakalpojumus;
      </li>
      <li>
        pirms slēgšanas Tu būsi atbildīgs par visām neizpildītajām saistībām,
        kas izriet no šī lietotāja līguma, kas saistīts ar Tavu kontu;
      </li>
      <li>
        mēs varam glabāt Tava konta informāciju mūsu datu bāzē, lai izpildītu
        mūsu juridiskās saistības; un
      </li>
      <li>
        mēs varam saglabāt Tavu elektronisko valūtu pēc slēgšanas tādā apjomā un
        uz laiku, kāds mums pamatoti nepieciešams, lai aizsargātu mūs un / vai
        jebkuru trešo personu pret jebkāda veida saistību risku. Lūdzu, sazinies
        ar mums, ja Tev ir kādi jautājumi par līdzekļiem, kas atrodas Tavā kontā
        slēgšanas brīdī.
        <strong>
          Ja esi rīcībnespējīga vai miruša konta turētāja likumīgais pārstāvis,
          lūdzu, sazinieties ar mums, lai saņemtu palīdzību.
        </strong>
      </li>
    </ul>

    <p>
      Gadījumā, ja kāda iemesla dēļ mēs nolemjam pārtraukt kādu no mūsu
      pakalpojumiem vai jebkuru mūsu pakalpojumu daļu vai funkciju, mēs Tev
      paziņosim vismaz 60 dienas iepriekš, pirms pakalpojuma vai funkcijas
      pārtraukšanas, ja vien mēs labticīgi nenolemjam, ka:
    </p>
    <ul>
      <li>
        pakalpojums vai funkcija ir jāpārtrauc ātrāk, kā to pieprasa likumi vai
        trešo personu attiecības; vai
      </li>
      <li>
        šādi rīkojoties, var rasties drošības risks vai būtisks ekonomisks vai
        materiāli tehnisks slogs.
      </li>
    </ul>

    <h3>Vairāku vietējo valūtu pārvaldīšana</h3>

    <p>
      Atverot kontu, tas ir konfigurēts lietošanai ar Tava konta sākuma valūtu.
    </p>

    <p>
      Tava konta sākuma valūta ir Zime (Z), ja trešās personas piegādātāja
      noteikumi nenosaka citādi.
    </p>

    <p>
      Tu vari konfigurēt savu kontu tā, lai to varētu izmantot vietējās valūtas
      glabāšanai un maksājumu nosūtīšanai un saņemšanai vietējā valūtā, kas nav
      Tava konta sākuma valūta. Ja Tev ir LC konta pārpalikums, mēs varam ļaut
      Tev to konvertēt citā vietējā valūtā.
    </p>

    <p>
      Ja Tavs ZĪME.LV norēķinu konta atlikums nav pietiekams, lai segtu
      maksājuma summu, kuru Tu uzdod mums veikt, mēs varam veikt pārskaitījumu
      no jebkura atlikuma citā Tavā kontā, lai segtu iztrūkumu. Var būt daži
      ierobežojumi tam, uz kurieni Tu vari sūtīt maksājumus noteiktās vietējās
      valūtās.
    </p>

    <p>
      Mēs varam ļaut Tev izvēlēties veidu, kādā tavs konts apstrādā un / vai
      konvertē maksājumus, kas saņemti vietējās valūtās, kas nav Tava konta
      sākuma valūta. Lai saņemtu pārskaitījumu valūtā, kas nav Tava konta sākuma
      valūta, var būt nepieciešams izveidot atlikumu šajā valūtā vai konvertēt
      pārskaitījumu citā valūtā, kuru mēs atļaujam Tev turēt.
    </p>

    <p>
      Maksājumus noteiktās vietējās valūtās var saņemt tikai automātiski
      konvertējot pārskaitījumu uz citu valūtu, kuru mēs Tev ļaujam turēt.
    </p>

    <p>
      Mēs varam pēc saviem ieskatiem noteikt ierobežojumus vietējās valūtas
      summai, kuru vari turēt, konvertēt, vai konvertējumu skaitam, ko vari
      veikt. Tu nedrīksti pārvaldīt vai konvertēt valūtas spekulatīvas
      tirdzniecības mērķim, konvertācijas arbitrāžas, konvertēšanas iespēju vai
      citu darbību dēļ, par kuru galveno mērķi mēs uzskatām peļņas gūšanu,
      pamatojoties uz valūtas konvertācijas likmēm. Mēs varam aizturēt, atcelt
      vai atsaukt jebkuru darījumu, kuru mēs uzskatām par šīs politikas
      pārkāpumu.
    </p>
  </div>
);
